.org-details-shell {
  min-height: 100vh;
  padding: 60px 24px;
  font-family: "Poppins", sans-serif;
  text-align: center;
}

/* SUBTITLE */

.subtitle {
  color: #777777;
  margin-bottom: 32px;
}

/* STEPPER */

.org-stepper {
  display: flex;
  justify-content: center;
  gap: 32px;
  margin-bottom: 30px;
}

.step {
  opacity: 0.4;
  font-weight: 600;
}

.step.active {
  opacity: 1;
  color: #7f00ff;
}

/* FORM */

.details-form {
  max-width: 420px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 14px;
}

.details-form label {
  font-size: 14px;
  font-weight: 600;
  text-align: left;
  color: #333333;
}

.details-form input,
.details-form select {
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid #e6e6e9;
  font-size: 14px;
  transition: border-color 0.25s ease, box-shadow 0.25s ease;
}

.details-form input:focus,
.details-form select:focus {
  outline: none;
  border-color: #7f00ff;
  box-shadow: 0 4px 14px rgba(127, 0, 255, 0.15);
}

/* CONTINUE BUTTON */

.continue-btn {
  margin-top: 20px;
  padding: 14px;
  border-radius: 40px;
  border: none;
  background: #7f00ff;
  color: #ffffff;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.continue-btn:hover {
  background: #6a00d6;
  transform: translateY(-2px);
}
